<template>
<nav class="nav has-shadow">
  <div class="container">
    <div class="nav-left">
      <a class="nav-item is-tab is-hidden-mobile is-active">Home</a>
      <a class="nav-item is-tab is-hidden-mobile">Features</a>
      <a class="nav-item is-tab is-hidden-mobile">Pricing</a>
      <a class="nav-item is-tab is-hidden-mobile">About</a>
    </div>
    <span class="nav-toggle">
    <span></span>
    <span></span>
    <span></span>
  </span>
    <div class="nav-right nav-menu">
      <a class="nav-item is-tab is-hidden-tablet is-active">Home</a>
      <a class="nav-item is-tab is-hidden-tablet">Features</a>
      <a class="nav-item is-tab is-hidden-tablet">Pricing</a>
      <a class="nav-item is-tab is-hidden-tablet">About</a>
      <a class="nav-item is-tab">
        <figure class="image is-16x16" style="margin-right: 8px;">
          <img src="http://bulma.io/images/jgthms.png">
        </figure>
        Profile
      </a>
      <a class="nav-item is-tab" @click="logout()">Log out</a>
    </div>
  </div>
</nav>
</template>

<script>
export default {

  name: 'Navbar',
  methods: {
    logout(){
      let access_token = localStorage.getItem('swagger_accessToken');
      this.$http.post('http://localhost:3000/api/usuarios/logout', access_token).then(res => {
        //fecha de expiracion guardad como INT
        
      localStorage.removeItem('swagger_accessToken');
      //localStorage.removeItem('userId');
      //localStorage.removeItem('created');
      console.log(localStorage.getItem('swagger_accessToken'));
      this.$router.push('/');
      }, res=>{
        //error
        console.log(localStorage);
        console.log(res.body);
        localStorage.removeItem('swagger_accessToken');
        console.log(localStorage.getItem('swagger_accessToken'));
      });
    }
  }

};
</script>

<style lang="css" scoped>
</style>


