export default function (Vue) {
	Vue.auth = {
		setToken(token, userId, expi){
			localStorage.setItem('access_token', token)
			localStorage.setItem('userId', userId)
			let date = new Date(expi).getTime()
			localStorage.setItem('expi', date)
		},

		getToken(){
			var token = localStorage.getItem('access_token');
			var userId = localStorage.getItem('userId');
			var expi = localStorage.getItem('expi', expi);

			if (!token )
				return null;

			if(Date.now() > parseInt(expi)){
				this.destroytoken();
				return null;
			} else{
				return token;
			}
		},
		
		destroytoken(){
			localStorage.removeItem('access_token');
			localStorage.removeItem('userId');
			localStorage.removeItem('expi');
		},

		isAuthd(){
			if(this.getToken()){
				return true;
			} else {
				return false;
			}
		},
	}

	Object.defineProperties(Vue.prototype, {
		$auth: {
			get(){
				return Vue.auth;
			}
		}
	})
}


